.certificatesContainer {
    max-width: 2000px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    .containerCards {
        justify-content: center;
        display: flex;
        max-width: 100%;
        flex-wrap: wrap;
        padding-bottom: 130px;
        margin-left: 30px
    }
    
    .certificado {
        max-width: 260px;
        height: 217px;
        width: 100%;
        background-color: white;
        margin-top: 50px;
        border-radius: 2px;
        box-shadow: -8px 10px 0px 0px rgba(3,14,79,1);
        padding-right: -10px;
    }
    
    h2 {
        font-size: 14px;
        font-weight: bold;
        text-align: center;
        max-width: 227.44px;
        width: 100%;
        color: #030E4F;
        padding-top: 28px;
    }
    
    .teste {
        width: 90%;
        margin: 0 auto;
    }
    
    .flex {
        width: 100%;
        margin-left: 40px;
        margin-top: 20px;
    }
    
    .img {
        max-width: 100px;
        width: 100%;
        margin-top: 10px;
        margin-right: 10px;
    }
    
    .arrow {
        margin-top: 12px;
        margin-left: 190px;
        cursor: pointer;
    }
    
    .arrow2 {
        margin-top: 0px;
        margin-left: 190px;
        cursor: pointer;
    }
    
    .flipContainer {
        width: 300px;
        height: 200px;
        perspective: 1000px;
    }
    
    .flipper{
        width: 100%;
        height: 100%;
        transition: transform 0.8s;
        transform-style: preserve-3d;
    }
    
    .flipper{
        position: relative;
    }
    .front, .back{
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
    }
    
    .back{
        transform: rotateY(180deg);
    }
    
    .switch:checked ~ .flipContainer .flipper {
        transform: rotateY(180deg);
      }
    
    .switch{
        display: none;
    }
    
